import React from 'react'
import './style.css'
import {Container} from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Grid from '@material-ui/core/Grid';
import {withStyles} from '@material-ui/core/styles';

class Presentation extends React.Component{

    render(){
        const { classes } = this.props;
        return(
            <>
                {/* <Paper xs = {12} className={classes.paper}> */}
                <Grid item lg="12">
                    <Container maxWidth="lg" className={classes.root}>
                        <Grid container spacing={3}>

                        <iframe width="560" height="315" src="https://www.youtube-nocookie.com/embed/videoseries?list=PLYWlkq63B8AVliuXddSJ-xKJyY9y58u61" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                        </Grid>
                    </Container>
                </Grid>
                <Grid item lg="12">
                    <Container maxWidth="lg" className={classes.root}>
                        <Grid container spacing={3}>

                        <iframe width="560" height="315" src="https://www.youtube-nocookie.com/embed/videoseries?list=PLYWlkq63B8AUcZB7PywmTgSadFuqhBh8X" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                        </Grid>
                    </Container>
                </Grid>
                
                {/* </Paper>  */}
            </>
        )
    }
}

const styles = theme => ({
    root : {
        // padding:'5% 2%'
    },
    paper : {
        padding: theme.spacing(2),
        textAlign: 'center',
        display: "inline-block",
    }
});

export default withStyles(styles)(Presentation);