import React from 'react'

import {withStyles} from '@material-ui/core/styles'
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
// import Paper from '@material-ui/core/Paper';
import { Card, CardMedia, CardContent, CardActions, Grid } from '@material-ui/core';
import _placeholder from '../../images/placeholder.png';

import YouTubeLogo from '../../images/social/YouTube_full-color_icon_(2017).svg'
import TwitchLogo from '../../images/social/twitch-seeklogo.com.svg'

const rows = {
    'monday' : [
        ["youtube", "6:00pm", "ðŸ”´Open World/Action/Others"],
        ["twitch", "6:00pm", "ðŸ”´Open World/Action/Others"]
    ],
    'tuesday': [["youtube", "6:00pm", "Open World/Action/Others"]],
    'wednesday': [["youtube", "6:00pm", "Open World/Action/Others"]],
    'thursday': [["youtube", "6:00pm", "Open World/Action/Others"]],
    'friday': [["youtube", "6:00pm", "Open World/Action/Others"]],
    'saturday': [["youtube", "6:00pm", "Open World/Action/Others"]],
    'sunday': [["youtube", "6:00pm", "Open World/Action/Others"]],
};

class Presentation extends React.Component {
    constructor(props) {
        super(props);
    }
    createCards = (classes) => {

        const self = this
        const days = ["monday","tuesday","wednesday","thursday","friday","saturday","sunday"]
        const cards = days.map(day => {

            return self.scheduleCard(classes,day, rows[day])
        });
        
        return cards;
    }

    setPlatform = (classes,value) => {
        if(value == "youtube")
            return(<img src={YouTubeLogo} className={classes.Logo}/>)
        else
            return(<img src={TwitchLogo} className={classes.Logo}/>)
    }

    setContent = (classes, platform, time, title) => {

        return (
            <div key={platform}>
                {this.setPlatform(classes,platform)}
                                {title}
                                <div>
                                {time}
                                </div>
            </div>
        )
    }

    scheduleCard = (classes,day,row) => {

        return (

                

                    <Grid item xs={12} lg={4} md={6} key={day}
                    >
                        <Card
                            className={classes.Card}
                        >

                            <CardContent 
                                // className={classes.CardContent}
                                justifyContent="center"
                                alignItems="center"
                            >
                                
                                {row.map(r => this.setContent(classes,...r))}
                                
                            </CardContent >

                            {/* Footer */}
                            <CardActions align="center" className={classes.CardActions} bg="black" justifyContent="center">
                                <h1 style={{ fontFamily: 'Righteous, cursive' }}>
                                    {day.toUpperCase()}
                                </h1>
                            </CardActions>
                        </Card>
                    </Grid>

                
            
        )
    }

    render(){
        const { classes } = this.props;
        return(
            <>
            <Grid
                    container
                    spacing={3}
                    direction="row"
                    alignItems="center"
                    justifyContent="center"
                    // style={{ minHeight: '100vh' }}
                >
                {/* <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="simple table">
                        <TableHead>
                        <TableRow>
                            <TableCell>Sunday [ðŸ”´LIVE]</TableCell>
                            <TableCell>Monday</TableCell>
                            <TableCell>Tuesday</TableCell>
                            <TableCell>Wednesday</TableCell>
                            <TableCell>Thursday</TableCell>
                            <TableCell>Friday [ðŸ”´LIVE]</TableCell>
                            <TableCell>Saturday [ðŸ”´LIVE]</TableCell>
                        </TableRow>
                        </TableHead>
                        <TableBody>
                            <TableRow key="schedule">
                                <TableCell>Open World/Action/Others</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>Grand Theft Auto: 5 *gameplays*</TableCell>
                                <TableCell>FPS/Competitive Games</TableCell>
                                <TableCell>Survival/Others</TableCell>
                            </TableRow>
                            <TableRow key="schedule">
                                <TableCell>GTA Online, Fall Guys, Roblox</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>Fortnite, CS:GO, Valorant, PUBG Mobile</TableCell>
                                <TableCell>Minecraft</TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>
                </TableContainer> */}
                {this.createCards(classes)}
                </Grid>
            </>
        )
    }
}

const styles = theme => ({
    root : {
        // padding:'5% 2%'
    },
    table: {
        width: "100%"
    },
    paper : {
        padding: theme.spacing(2),
        textAlign: 'center'
    },
    Logo: {
        maxHeight: '20px'
    },
    Card:{
        // margin: "1em"
    },
    CardMedia: {
        // paddingTop: '56.25%', // 16:9
        objectFit: 'cover',
        objectPosition: 'center',
        // height: '70px',
        // width: '200px'
    },
    CardActions:{
        padding: "2em",
        display: 'block',
        fontFamily: 'Righteous, cursive',
        letterSpacing: '0.15em'
    }
});

export default withStyles(styles)(Presentation);